import moduleController from '../controllers/ModuleController';
import { BlurModule } from '../modules/blurModule';
import { loadWorker } from '../helpers/workers';
import c from '../context';

export default async () => {
  const worker = await loadWorker(`${c.chunkPublicPath}blurWorker.js`);
  const blurModule = new BlurModule(worker);
  moduleController.setModule('module', 'blur', blurModule);
};
