export class BlurModule {
  worker: Worker;
  constructor(worker: Worker) {
    this.worker = worker;
    this.worker.postMessage({
      fn: 'init',
    });
  }

  process(imageData: ImageData) {
    return new Promise((resolve) => {
      this.worker.postMessage({
        fn: 'process',
        iData: imageData,
      });
      this.worker.onmessage = async ({ data: { blurConfidence } }) => {
        resolve(blurConfidence);
      };
    });
  }

  drop() {
    this.worker.postMessage({ fn: 'drop' });
  }
}
